  --fifth-color: #ffeee2;
  --sixth-color: #f6f8fd;
  --white-color: #ffffff;
  --hover-color: #ffab6a;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; /* 1REM = 10px */
}
*,
*:before,
s *:after {
